@charset "UTF-8";
/* CSS Document */
html, body {
  margin: 0;
  padding: 0;
  font: normal 16px/1.5 Arial, "微軟正黑體";
  color: #333333;
  background-color: #f4f4f4; }

a {
  text-decoration: none;
  border: none;
  outline: none;
  color: 333333;
  outline: none;
  /* for Firefox */
  hlbr: expression(this.onFocus=this.blur());
  /* for IE */
  cursor: pointer; }

a:hover {
  color: red;
  text-decoration: none; }

li {
  list-style: none; }

div, p, span, ul, ol, li, h1, h2, h3, h4, h5, h6, input, table, td, th, img {
  margin: 0;
  padding: 0; }

img {
  border: none;
  display: block; }

.clr {
  clear: both;
  display: block; }

.tR {
  text-align: right; }

.tL {
  text-align: left; }

.tC {
  text-align: center; }

.fL {
  float: left; }

.fR {
  float: right; }

.phone {
  display: none; }

.m_hide {
  display: block; }

.m_block {
  display: none; }

h2 {
  letter-spacing: 0.5px;
  font-size: 30px; }

h3 {
  letter-spacing: 0.5px;
  font-size: 24px; }

/*頁面設定*/
#pagesize {
  position: relative;
  margin: 0 auto; }

/*header*/
header {
  position: relative;
  height: 80px;
  background: #fff url(../images/header_bg.png) right no-repeat;
  background-size: auto 100%;
  border-bottom: 45px solid transparent;
  -moz-border-image: -moz-linear-gradient(left, #093d7c 0%, #0081cc 100%);
  -webkit-border-image: -webkit-linear-gradient(left, #093d7c 0%, #0081cc 100%);
  border-image: linear-gradient(to right, #093d7c 0%, #0081cc 100%);
  border-image-slice: 1; }

header h1 {
  position: absolute;
  left: 40px;
  top: 20px; }

header h1 a {
  width: 340px;
  height: 40px;
  display: block;
  background: url(../images/logo.png) no-repeat; }

header h2 {
  position: absolute;
  right: 40px;
  top: 20px;
  font-size: 25px;
  color: #0358aa; }

header h2 a {
  color: #0358aa; }

header nav {
  position: absolute;
  top: 80px;
  left: 40px;
  width: calc(100% - 80px);
  height: 45px;
  display: block;
  text-align: right; }

header nav ul li {
  line-height: 45px;
  display: inline-block;
  background: url(../images/nav_bg.png) right no-repeat; }

header nav ul li a {
  position: relative;
  height: 45px;
  padding: 0 30px;
  display: block;
  color: #fff; }

header nav ul li:last-child {
  background: none; }

header nav ul li:last-child a {
  padding: 0 0 0 30px; }

header nav ul li a:hover, header nav ul li.current a {
  color: #ffde00; }

header nav ul li.current a::after {
  z-index: 1;
  position: absolute;
  bottom: -20px;
  right: 50%;
  margin: 0 -10px;
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-top-color: #0081cc;
  content: '';
  pointer-events: none; }

header .navSub {
  z-index: 1;
  position: absolute;
  right: 0;
  top: 125px;
  width: 100%;
  display: block;
  background-color: rgba(249, 249, 249, 0.95); }

header .navSub a.close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 20px;
  height: 20px;
  display: block;
  background: url(../images/bt_close.png) no-repeat; }

header .in {
  width: 1000px;
  padding: 30px 20px;
  margin: 0 auto;
  background: none;
  display: flex;
  justify-content: space-around;
  box-sizing: border-box; }

header .in h3 {
  font-size: 16px;
  font-weight: bold;
  line-height: 36px;
  color: #1886e3;
  border-bottom: 3px solid #d8d9da;
  padding: 0;
  margin: 0;
  text-align: left;
  background: none; }

header .in h3 a {
  color: #1886e3; }

header .in h3.trig {
  padding: 0 20px 0 0;
  background-size: 16px; }

header .in h3.trig.close {
  background-size: 16px; }

header .in ul {
  background: none;
  display: block !important; }

header .in ul li {
  font-size: 15px;
  line-height: 36px; }

header .in ul li a {
  color: #444444; }

/*table*/
table {
  margin: 0 auto 30px; }

table th {
  padding: 12px 20px;
  background-color: #9c9c9c;
  color: #ffffff;
